import React, { useState } from 'react';
// material-ui
import {
    Avatar,
    CardContent,
    CardActions,
    Card,
    Stack,
    Typography,
    Container,
    Chip,
    Grid,
    Button,
    Paper,
    TextField,
    ListItemAvatar,
    ListItemButton,
    ListItemSecondaryAction,
    Tooltip
} from '@mui/material';

// project import
import { Dialog, Slide } from '@mui/material';
import { FormControl, MenuItem, Select, InputLabel } from '@mui/material';
import { useContext } from 'react';
import { AuthContext } from 'src/App';
import axios from 'axios';
import { apiBaseURL } from 'src/config-global';
import { toast } from 'react-toastify';
import { useNavigate } from 'react-router';
import { useAuthContext } from 'src/auth/useAuthContext';

// ==============================|| SAMPLE PAGE ||============================== //

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="left" ref={ref} {...props} />;
});

const HelpCenter = () => {
    const { user } = useAuthContext();
    const navigate = useNavigate();

    const [open, setOpen] = React.useState(false);
    const [title, settitle] = React.useState('');
    const [department, setdepartment] = React.useState('none');

    const handleChange = (event) => {
        setdepartment(event.target.value);
    };

    const handleClickOpen = (formTitle) => {
        setOpen(true);
        settitle(formTitle);
    };
    const handleClose = () => {
        setOpen(false);
    };

    const [ticket, setTicket] = useState({
        title: '',
        desc: ''
    });

    const handleTicket = (field) => (e) => {
        setTicket((prev) => ({
            ...prev,
            [field]: e.target.value
        }));
    };

    const handleDialogSubmit = () => {
        if (title === 'Create Ticket') {
            const formData = { ...ticket, email: user.email, company: user.company, name: user.displayName, photoURL: user.photoURL };
            axios.post(`${apiBaseURL}/tickets`, { formData }).then((res) => {
                toast.success('Ticket Opened');
                handleClose();
                axios
                    .post(`${apiBaseURL}/email/send?service=ticket&event=open`, {
                        formData: {
                            to: res.data.email,
                            ref: res.data
                        }
                    })
                    .then((res) => {
                        console.log(res.data);
                    });
                setTicket({
                    title: '',
                    desc: ''
                });
            });
        }
    };

    return (
        <>
            <Dialog fullScreen open={open} onClose={handleClose} TransitionComponent={Transition}>
                <Container sx={{ mt: 6, mb: 6 }} maxWidth="sm">
                    <Card variant="outlined">
                        <CardContent>
                            <div style={{ display: 'flex', justifyContent: 'center' }}>
                                <Typography variant="h2">{title}</Typography>
                            </div>
                            <br />
                            {title === 'Create Ticket' ? (
                                <>
                                    {/* <Stack direction="row" spacing={2}>
                                        <div>
                                            <Typography variant="p">
                                                First Name <span style={{ color: 'red' }}>*</span>
                                            </Typography>
                                            <TextField type="text" variant="outlined" fullWidth />
                                        </div>
                                        <div>
                                            <Typography variant="p">Middle Name</Typography>
                                            <TextField type="text" variant="outlined" fullWidth />
                                        </div>
                                        <div>
                                            <Typography variant="p">
                                                Surame <span style={{ color: 'red' }}>*</span>
                                            </Typography>
                                            <TextField type="text" variant="outlined" fullWidth />
                                        </div>
                                    </Stack>
                                    <br />
                                    <Typography variant="p">Email</Typography>
                                    <TextField type="text" variant="outlined" fullWidth />
                                    <br />
                                    <br /> */}
                                    <Typography variant="p">Title</Typography>
                                    <TextField
                                        type="text"
                                        variant="outlined"
                                        value={ticket.title}
                                        fullWidth
                                        onChange={handleTicket('title')}
                                    />
                                    <br />
                                    <br />
                                    {/* <FormControl sx={{ mt: 1 }} fullWidth>
                                        <InputLabel id="demo-simple-select-label">Department</InputLabel>
                                        <Select
                                            labelId="demo-simple-select-label"
                                            id="demo-simple-select"
                                            value={department}
                                            label="department"
                                            onChange={handleChange}
                                        >
                                            <MenuItem value="none">None</MenuItem>
                                            <MenuItem value="dep-1">Department 1</MenuItem>
                                            <MenuItem value="dep-2">Department 2</MenuItem>
                                            <MenuItem value="dep-3">Department 3</MenuItem>
                                        </Select>
                                    </FormControl>

                                    <br />
                                    <br /> */}
                                    <Typography variant="p">Description</Typography>
                                    <TextField
                                        variant="outlined"
                                        fullWidth
                                        type="text"
                                        value={ticket.desc}
                                        onChange={handleTicket('desc')}
                                        multiline
                                        rows={6}
                                    />
                                </>
                            ) : null}
                        </CardContent>

                        <CardActions sx={{ float: 'right' }} style={{ padding: '0px 20px 20px' }}>
                            <Button onClick={handleClose}>Close</Button>
                            <Button onClick={handleDialogSubmit} variant="contained">
                                Submit
                            </Button>
                        </CardActions>
                    </Card>
                </Container>
            </Dialog>

            <Grid container rowSpacing={4.5} columnSpacing={2.75}>
                <Grid item xs={12}>
                    <Typography sx={{ float: 'left' }} variant="h3">
                        Submit Ticket
                    </Typography>
                </Grid>

                <Grid item xs={12}>
                    <Grid container rowSpacing={4.5} columnSpacing={2.75}>
                        {/* <Grid item xs={3}>
                            <Card variant="outlined" sx={{ cursor: 'pointer' }}>
                                <CardContent>
                                    <center>
                                        <img alt="Self Service" src="https://img.icons8.com/ios-filled/100/null/guest-male--v1.png" />
                                        <Typography variant="h5">Self Service</Typography>
                                        <Typography variant="p">
                                            Please check our list for frequently asked question in the self service section before
                                            submitting a ticket for self service troubleshooting.
                                        </Typography>
                                    </center>
                                </CardContent>
                            </Card>
                        </Grid> */}
                        <Grid item xs={6}>
                            <Card
                                variant="outlined"
                                sx={{ cursor: 'pointer' }}
                                onClick={() => {
                                    handleClickOpen('Create Ticket');
                                }}
                            >
                                <CardContent>
                                    <center>
                                        <img alt="Submit Ticket" src="https://img.icons8.com/fluency/100/null/add-ticket.png" />
                                        <Typography variant="h5">Submit Ticket</Typography>
                                        <Typography variant="p">
                                            Submit your ticket via clicking here
                                        </Typography>
                                    </center>
                                </CardContent>
                            </Card>
                        </Grid>
                        <Grid item xs={6}>
                            <Card
                                variant="outlined"
                                sx={{ cursor: 'pointer' }}
                                onClick={() => {
                                    navigate('/dashboard/tickets');
                                }}
                            >
                                <CardContent>
                                    <center>
                                        <img alt="Check Status" src="https://img.icons8.com/color/100/null/ok--v1.png" />
                                        <Typography variant="h5">Check Status</Typography>
                                        <Typography variant="p">
                                            Check status of your ticket
                                        </Typography>
                                    </center>
                                </CardContent>
                            </Card>
                        </Grid>
                        {/* <Grid item xs={3}>
                            <Card variant="outlined" sx={{ cursor: 'pointer' }}>
                                <CardContent>
                                    <center>
                                        <img alt="FAQs" src="https://img.icons8.com/ios-filled/100/null/ask-question.png" />
                                        <Typography variant="h5">FAQs</Typography>
                                        <Typography variant="p">
                                            Please check our list for frequently asked question in the self service section before
                                            submitting a ticket for self service troubleshooting.
                                        </Typography>
                                    </center>
                                </CardContent>
                            </Card>
                        </Grid> */}
                    </Grid>
                </Grid>
            </Grid>
        </>
    );
};

export default HelpCenter;
